body{
  background-color: #282c34;
  color:white;
  
  text-align: center;
  border:4px solid lightblue;
}

.shopBox{
  max-height:300px;
  min-height: 250px;
  text-align: center;
  display:inline-table;
  
  border:1px solid black;
  margin:1px;
  padding: 1px;
  background-color: white;
  color:black;
  border-radius: 6%;
}

.shopBox:hover{
  max-height:310px;
  min-height: 255px;
  text-align: center;
  display:inline-table;
  
  border:2px solid black;
  margin:2px;
  padding: 2px;
  background-color: white;
  color:black;
  border-radius: 10%;
}

button{
  width:120px;
  height:80px;
  border-radius: 90%;
  color:blue;
  background-color: orange;
  outline:none; 
  text-shadow: orangered 10px 10px 10px 5px;
  font-weight: 600;
  font-size: 20px;
  
}
button:active{
  width:122px;
  height:83px;
  border-radius: 99%;
  color:blue;
  background-color: red;
  outline:none; 
  text-shadow: orangered 10px 10px 10px 5px;
  font-weight: 700;
  font-size: 25px;
  
}

img{
  max-width: 125px;
}

h3{
  text-decoration: underline blue;
  color:blue
}